// Types
interface Ship {
  ship_id: string
  ship_name: string
  ship_class?: string
  ship_category?: string
  command?: string
}

interface ToolCall {
  name: string
  arguments: Record<string, any>
  result?: any
}

interface Message {
  role: "user" | "assistant"
  content: string
  timestamp: string
  tool_calls?: ToolCall[]
  isError?: boolean
}

interface ChatState {
  messages: Message[]
  isLoading: boolean
  error: string | null
  retryCount: number
}

interface AutocompletePosition {
  top: number
  left: number
}

interface ShipWithScore {
  ship: Ship
  score: number
}

interface ReliabilityData {
  name: string
  reliability: string
  ship: string
}

interface ChatErrorBoundaryState {
  hasError: boolean
  error: Error | null
}

interface ChatErrorBoundaryProps {
  children: React.ReactNode
}

// Error Boundary Component
class ChatErrorBoundary extends React.Component<ChatErrorBoundaryProps, ChatErrorBoundaryState> {
  constructor(props: ChatErrorBoundaryProps) {
    super(props)
    this.state = { hasError: false, error: null }
  }

  static getDerivedStateFromError(error: Error): ChatErrorBoundaryState {
    return { hasError: true, error }
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    console.error("Chat Error:", error, errorInfo)
  }

  render() {
    if (this.state.hasError) {
      return (
        <div className="flex-1 flex items-center justify-center p-8">
          <div className="text-center space-y-6 max-w-md">
            <div className="w-16 h-16 mx-auto bg-destructive/10 rounded-full flex items-center justify-center">
              <Bot className="w-8 h-8 text-destructive" />
            </div>
            <div className="space-y-3">
              <h3 className="text-xl font-semibold text-destructive">Something went wrong</h3>
              <p className="text-muted-foreground leading-relaxed">
                We encountered an unexpected error. Please refresh the page to continue your conversation.
              </p>
            </div>
            <Button onClick={() => window.location.reload()} className="px-6 py-2.5 font-medium">
              Refresh Page
            </Button>
          </div>
        </div>
      )
    }
    return this.props.children
  }
}

// Fuzzy search implementation
const fuzzySearch = (query: string, ships: Ship[]): Ship[] => {
  if (!query.trim()) return ships.slice(0, 10)

  const searchTerm = query.toLowerCase()

  return ships
    .map((ship): ShipWithScore | null => {
      const name = ship.ship_name.toLowerCase()
      const class_ = ship.ship_class?.toLowerCase() || ""
      const category = ship.ship_category?.toLowerCase() || ""

      // Exact match gets highest score
      if (name === searchTerm) return { ship, score: 100 }

      // Starts with search term
      if (name.startsWith(searchTerm)) return { ship, score: 90 }

      // Contains search term
      if (name.includes(searchTerm)) return { ship, score: 80 }

      // Class or category match
      if (class_.includes(searchTerm) || category.includes(searchTerm)) {
        return { ship, score: 70 }
      }

      // Fuzzy character matching
      let score = 0
      let searchIndex = 0

      for (let i = 0; i < name.length && searchIndex < searchTerm.length; i++) {
        if (name[i] === searchTerm[searchIndex]) {
          score += 1
          searchIndex++
        }
      }

      // If we matched all characters, calculate fuzzy score
      if (searchIndex === searchTerm.length) {
        const fuzzyScore = (score / name.length) * 60
        return { ship, score: fuzzyScore }
      }

      return null
    })
    .filter((item): item is ShipWithScore => item !== null)
    .sort((a, b) => b.score - a.score)
    .slice(0, 10)
    .map((item) => item.ship)
}

// Debounce hook
const useDebounce = (value: string, delay: number): string => {
  const [debouncedValue, setDebouncedValue] = useState(value)

  useEffect(() => {
    const handler = setTimeout(() => {
      setDebouncedValue(value)
    }, delay)

    return () => {
      clearTimeout(handler)
    }
  }, [value, delay])

  return debouncedValue
}

