// Reliability Chart Component Props
interface ReliabilityChartProps {
  toolCalls: ToolCall[]
}

function ReliabilityChart({ toolCalls }: ReliabilityChartProps) {
  const getReliabilityChartData = (toolCalls: ToolCall[]): ReliabilityData[] | null => {
    if (!toolCalls || !Array.isArray(toolCalls)) return null

    const reliabilityTool = toolCalls.find((tool) => tool.name === "get_component_reliability")
    if (!reliabilityTool || !reliabilityTool.result) return null

    const result = reliabilityTool.result

    // Handle single component result
    if (result.data && result.data.reliability_score !== undefined) {
      return [
        {
          name: result.data.nomenclature || result.data.component_name || "Component",
          reliability: (result.data.reliability_score * 100).toFixed(2),
          ship: result.data.ship,
        },
      ]
    }

    // Handle multiple component results
    if (result.data && result.data.results && Array.isArray(result.data.results)) {
      return result.data.results
        .filter((item: any) => item.reliability !== null && item.reliability !== undefined)
        .map(
          (item: any): ReliabilityData => ({
            name: `${item.nomenclature || "Unknown"} (${item.ship || "Unknown Ship"})`,
            reliability: (item.reliability * 100).toFixed(2),
            ship: item.ship || "Unknown Ship",
          }),
        )
    }

    return null
  }

  interface CustomTooltipProps {
    active?: boolean
    payload?: Array<{
      payload: ReliabilityData
    }>
    label?: string
  }

  const CustomTooltip = ({ active, payload, label }: CustomTooltipProps) => {
    if (active && payload && payload.length) {
      const data = payload[0].payload
      return (
        <div className="bg-card border border-border/50 rounded-xl p-4 shadow-xl shadow-black/10 backdrop-blur-sm">
          <p className="font-semibold text-sm mb-2">{`${label}`}</p>
          <p className="text-primary font-medium text-sm">{`Reliability: ${data.reliability}%`}</p>
          <p className="text-xs text-muted-foreground mt-1">{`Ship: ${data.ship}`}</p>
        </div>
      )
    }
    return null
  }

  const chartData = getReliabilityChartData(toolCalls)
  if (!chartData || chartData.length === 0) return null

  return (
    <div className="mt-8">
      <div className="bg-card rounded-2xl border border-border/50 p-6 shadow-lg shadow-black/5 bg-gradient-to-br from-card to-card/80">
        <div className="flex items-center gap-3 mb-6">
          <div className="w-2 h-8 bg-primary rounded-full"></div>
          <h3 className="text-lg font-semibold text-card-foreground">Reliability Distribution</h3>
        </div>
        <div className="bg-background/30 rounded-xl p-4">
          <ResponsiveContainer width="100%" height={320}>
            <BarChart
              data={chartData}
              margin={{
                top: 20,
                right: 30,
                left: 20,
                bottom: 5,
              }}
            >
              <CartesianGrid strokeDasharray="3 3" className="stroke-border/30" />
              <XAxis
                dataKey="name"
                className="text-muted-foreground"
                tick={{ fontSize: 12 }}
                tickLine={{ stroke: "currentColor", strokeWidth: 0.5 }}
              />
              <YAxis
                className="text-muted-foreground"
                tick={{ fontSize: 12 }}
                tickLine={{ stroke: "currentColor", strokeWidth: 0.5 }}
                label={{ value: "Reliability (%)", angle: -90, position: "insideLeft" }}
              />
              <Tooltip content={<CustomTooltip />} />
              <Legend />
              <Bar
                dataKey="reliability"
                name="Equipment"
                fill="#4F46E5"
                radius={[6, 6, 0, 0]}
                className="drop-shadow-sm"
              />
            </BarChart>
          </ResponsiveContainer>
        </div>
        <div className="mt-4 p-3 bg-muted/30 rounded-lg">
          <div className="text-xs text-muted-foreground leading-relaxed">
            <span className="font-medium">Note:</span> Reliability scores are shown as percentages. Higher values
            indicate better equipment reliability.
          </div>
        </div>
      </div>
    </div>
  )
}

// Message Component Props
interface MessageProps {
  message: Message
  index: number
}

function Message({ message, index }: MessageProps) {
  const hasReliabilityToolCall = (toolCalls?: ToolCall[]): boolean => {
    if (!toolCalls || !Array.isArray(toolCalls)) return false
    return toolCalls.some((tool) => tool.name === "get_component_reliability")
  }

  return (
    <div
      className={`flex gap-4 ${message.role === "user" ? "justify-end" : "justify-start"} animate-in fade-in-0 slide-in-from-bottom-2 duration-500`}
      style={{ animationDelay: `${index * 100}ms` }}
    >
      {message.role === "assistant" && (
        <Avatar className="w-10 h-10 mt-1 shadow-md ring-2 ring-background">
          <div className="w-full h-full bg-gradient-to-br from-primary/10 to-primary/20 flex items-center justify-center">
            <Bot className="w-5 h-5 text-primary" />
          </div>
        </Avatar>
      )}
      <div className={`max-w-3xl ${message.role === "user" ? "order-first" : ""}`}>
        <div
          className={`rounded-2xl px-5 py-4 shadow-sm transition-all duration-200 hover:shadow-md ${
            message.role === "user"
              ? "bg-primary text-primary-foreground ml-auto shadow-primary/20"
              : message.isError
                ? "bg-destructive/10 text-destructive border border-destructive/20 shadow-destructive/10"
                : "bg-muted shadow-black/5"
          }`}
        >
          <div className="whitespace-pre-wrap leading-relaxed text-[15px]">{message.content}</div>

          {/* Reliability Chart */}
          {message.role === "assistant" && hasReliabilityToolCall(message.tool_calls) && message.tool_calls && (
            <ReliabilityChart toolCalls={message.tool_calls} />
          )}

          {/* Tool Calls Display */}
          {message.tool_calls && message.tool_calls.length > 0 && (
            <div className="mt-6 pt-4 border-t border-border/30">
              <div className="text-sm font-medium text-muted-foreground mb-3 flex items-center gap-2">
                <div className="w-1.5 h-1.5 bg-primary rounded-full"></div>
                Tool calls
              </div>
              <div className="space-y-3">
                {message.tool_calls.map((tool, toolIndex) => (
                  <div key={toolIndex} className="bg-background/60 rounded-xl p-4 border border-border/30">
                    <div className="font-semibold text-foreground text-sm mb-2">{tool.name}</div>
                    <div className="text-muted-foreground text-xs mb-3">
                      <div className="font-medium mb-1">Arguments:</div>
                      <pre className="bg-background/80 rounded-lg p-3 overflow-x-auto text-xs leading-relaxed">
                        {JSON.stringify(tool.arguments, null, 2)}
                      </pre>
                    </div>
                    {tool.result && (
                      <div className="text-muted-foreground">
                        <details className="cursor-pointer group">
                          <summary className="hover:text-foreground transition-colors duration-150 text-xs font-medium flex items-center gap-2">
                            <div className="w-1 h-1 bg-muted-foreground rounded-full group-open:bg-primary transition-colors duration-150"></div>
                            View result
                          </summary>
                          <div className="mt-3 bg-background/80 rounded-lg p-3 border border-border/20">
                            <pre className="text-xs overflow-x-auto leading-relaxed">
                              {JSON.stringify(tool.result, null, 2)}
                            </pre>
                          </div>
                        </details>
                      </div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
        <div className="text-xs text-muted-foreground mt-2 px-5 font-medium">
          {new Date(message.timestamp).toLocaleTimeString()}
        </div>
      </div>
      {message.role === "user" && (
        <Avatar className="w-10 h-10 mt-1 shadow-md ring-2 ring-background">
          <div className="w-full h-full bg-gradient-to-br from-muted to-muted/80 flex items-center justify-center">
            <User className="w-5 h-5 text-muted-foreground" />
          </div>
        </Avatar>
      )}
    </div>
  )
}

