// Main Chat Component
export default function ChatMain({ ships = [] }: ChatMainProps) {
  // Consolidated state
  const [chatState, setChatState] = useState<ChatState>({
    messages: [],
    isLoading: false,
    error: null,
    retryCount: 0,
  })

  // Input and autocomplete state
  const [inputValue, setInputValue] = useState("")
  const [searchQuery, setSearchQuery] = useState("")
  const [showAutocomplete, setShowAutocomplete] = useState(false)
  const [autocompletePosition, setAutocompletePosition] = useState<AutocompletePosition>({ top: 0, left: 0 })
  const [selectedIndex, setSelectedIndex] = useState(-1)

  const inputRef = useRef<HTMLInputElement>(null)
  const autocompleteRef = useRef<HTMLDivElement>(null)
  const abortControllerRef = useRef<AbortController | null>(null)

  // Debounced search query
  const debouncedSearchQuery = useDebounce(searchQuery, 200)

  // Memoized filtered ships with fuzzy search
  const filteredShips = useMemo(() => {
    return fuzzySearch(debouncedSearchQuery, ships)
  }, [debouncedSearchQuery, ships])

  // Extract ship names from the message
  const extractShipNames = useCallback((message: string): string[] => {
    const shipNamePattern = /@ship_name=([^@\s,]+(?:\s+[^@\s,]*)*)/g
    const matches: string[] = []
    let match

    while ((match = shipNamePattern.exec(message)) !== null) {
      const shipNamesString = match[1].trim()
      const shipNames = shipNamesString
        .split(",")
        .map((name) => name.trim())
        .filter((name) => name.length > 0)
      matches.push(...shipNames)
    }

    return [...new Set(matches)]
  }, [])

  // Send message function with proper error handling
  const sendMessage = async () => {
    if (!inputValue.trim() || chatState.isLoading) return

    // Cancel any ongoing request
    if (abortControllerRef.current) {
      abortControllerRef.current.abort()
    }

    const userMessage: Message = {
      role: "user",
      content: inputValue.trim(),
      timestamp: new Date().toISOString(),
    }

    setChatState((prev) => ({
      ...prev,
      messages: [...prev.messages, userMessage],
      isLoading: true,
      error: null,
    }))

    const messageToSend = inputValue.trim()
    const extractedShips = extractShipNames(messageToSend)
    setInputValue("")
    setShowAutocomplete(false)

    // Create new abort controller for this request
    abortControllerRef.current = new AbortController()

    try {
      const requestBody = {
        message: messageToSend,
        conversation_history: chatState.messages,
        filters: {
          ships: extractedShips,
          explain: false,
        },
      }

      const response = await fetch("http://127.0.0.1:8000/chat/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(requestBody),
        signal: abortControllerRef.current.signal,
      })

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`)
      }

      const data = await response.json()

      const assistantMessage: Message = {
        role: "assistant",
        content: data.response,
        timestamp: data.timestamp,
        tool_calls: data.tool_calls,
      }

      setChatState((prev) => ({
        ...prev,
        messages: [...prev.messages, assistantMessage],
        isLoading: false,
        retryCount: 0,
      }))
    } catch (error) {
      if (error instanceof Error && error.name === "AbortError") {
        return // Request was cancelled, don't update state
      }

      console.error("Error sending message:", error)

      const errorMessage: Message = {
        role: "assistant",
        content: `Sorry, I encountered an error: ${error instanceof Error ? error.message : "Unknown error"}. Please try again.`,
        timestamp: new Date().toISOString(),
        isError: true,
      }

      setChatState((prev) => ({
        ...prev,
        messages: [...prev.messages, errorMessage],
        isLoading: false,
        error: error instanceof Error ? error.message : "Unknown error",
        retryCount: prev.retryCount + 1,
      }))
    }
  }

  // Handle input changes and autocomplete logic
  const handleInputChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value
    setInputValue(value)

    const cursorPosition = e.target.selectionStart || 0
    const textBeforeCursor = value.substring(0, cursorPosition)
    const shipNameMatch = textBeforeCursor.match(/@ship_name=([^@]*?)([^@,\s]*)$/)

    if (shipNameMatch) {
      const searchTerm = shipNameMatch[2]
      setSearchQuery(searchTerm)
      const input = inputRef.current
      if (input) {
        const rect = input.getBoundingClientRect()
        setAutocompletePosition({
          top: rect.bottom + window.scrollY + 8,
          left: rect.left + window.scrollX,
        })
      }
      setShowAutocomplete(true)
      setSelectedIndex(-1)
    } else {
      setShowAutocomplete(false)
      setSearchQuery("")
    }
  }, [])

  // Handle ship selection
  const selectShip = useCallback(
    (ship: Ship) => {
      const cursorPosition = inputRef.current?.selectionStart || 0
      const textBeforeCursor = inputValue.substring(0, cursorPosition)
      const textAfterCursor = inputValue.substring(cursorPosition)

      const newText =
        textBeforeCursor.replace(/@ship_name=([^@]*?)([^@,\s]*)$/, (match, existingShips, currentSearch) => {
          const prefix = existingShips.trim() ? existingShips + ", " : ""
          return `@ship_name=${prefix}${ship.ship_name}`
        }) + textAfterCursor

      setInputValue(newText)
      setShowAutocomplete(false)
      setSelectedIndex(-1)
      setSearchQuery("")

      setTimeout(() => {
        inputRef.current?.focus()
        const newCursorPosition = newText.length - textAfterCursor.length
        inputRef.current?.setSelectionRange(newCursorPosition, newCursorPosition)
      }, 0)
    },
    [inputValue],
  )

  // Handle keyboard navigation
  const handleKeyDown = useCallback(
    (e: React.KeyboardEvent<HTMLInputElement>) => {
      if (!showAutocomplete) {
        if (e.key === "Enter" && !e.shiftKey) {
          e.preventDefault()
          sendMessage()
        }
        return
      }

      switch (e.key) {
        case "ArrowDown":
          e.preventDefault()
          setSelectedIndex((prev) => (prev < filteredShips.length - 1 ? prev + 1 : prev))
          break
        case "ArrowUp":
          e.preventDefault()
          setSelectedIndex((prev) => (prev > 0 ? prev - 1 : -1))
          break
        case "Enter":
          e.preventDefault()
          if (selectedIndex >= 0 && selectedIndex < filteredShips.length) {
            selectShip(filteredShips[selectedIndex])
          } else if (!e.shiftKey) {
            sendMessage()
          }
          break
        case "Escape":
          e.preventDefault()
          setShowAutocomplete(false)
          setSelectedIndex(-1)
          break
        case "Tab":
          if (selectedIndex >= 0 && selectedIndex < filteredShips.length) {
            e.preventDefault()
            selectShip(filteredShips[selectedIndex])
          }
          break
      }
    },
    [showAutocomplete, selectedIndex, filteredShips, selectShip],
  )

  // Close autocomplete when clicking outside
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (
        autocompleteRef.current &&
        !autocompleteRef.current.contains(event.target as Node) &&
        inputRef.current &&
        !inputRef.current.contains(event.target as Node)
      ) {
        setShowAutocomplete(false)
        setSelectedIndex(-1)
      }
    }

    document.addEventListener("mousedown", handleClickOutside)
    return () => document.removeEventListener("mousedown", handleClickOutside)
  }, [])

  // Handle quick actions from welcome screen
  const handleQuickAction = useCallback((action: string) => {
    setInputValue(action)
    // Focus the input after setting the value
    setTimeout(() => {
      inputRef.current?.focus()
    }, 0)
  }, [])

  // Cleanup abort controller on unmount
  useEffect(() => {
    return () => {
      if (abortControllerRef.current) {
        abortControllerRef.current.abort()
      }
    }
  }, [])

  return (
    <ChatErrorBoundary>
      <div className="bg-gradient-to-br from-background via-background to-background/95 border border-border/30 shadow-2xl shadow-black/10 rounded-3xl flex-1 flex flex-col relative mx-4 my-4 backdrop-blur-sm overflow-hidden">
        {/* Autocomplete Dropdown */}
        <AutocompleteDropdown
          show={showAutocomplete}
          ships={filteredShips}
          position={autocompletePosition}
          selectedIndex={selectedIndex}
          onSelect={selectShip}
          onMouseEnter={setSelectedIndex}
          forwardRef={autocompleteRef}
        />

        {/* Chat Content */}
        <div className="flex-1 flex flex-col overflow-hidden">
          {chatState.messages.length === 0 ? (
            <WelcomeScreen onQuickAction={handleQuickAction} />
          ) : (
            <div className="flex-1 overflow-y-auto p-8 scrollbar-thin scrollbar-thumb-border/30 scrollbar-track-transparent">
              <div className="max-w-4xl mx-auto space-y-8">
                {chatState.messages.map((message, index) => (
                  <Message key={index} message={message} index={index} />
                ))}
                {chatState.isLoading && (
                  <div className="flex gap-4 justify-start animate-in fade-in-0 slide-in-from-bottom-2 duration-500">
                    <Avatar className="w-10 h-10 mt-1 shadow-md ring-2 ring-background">
                      <div className="w-full h-full bg-gradient-to-br from-primary/10 to-primary/20 flex items-center justify-center">
                        <Bot className="w-5 h-5 text-primary" />
                      </div>
                    </Avatar>
                    <div className="bg-muted rounded-2xl px-5 py-4 shadow-sm">
                      <div className="flex items-center gap-3">
                        <Loader2 className="w-5 h-5 animate-spin text-primary" />
                        <span className="text-muted-foreground font-medium">Thinking...</span>
                      </div>
                    </div>
                  </div>
                )}
              </div>
            </div>
          )}
        </div>

        {/* Chat Input */}
        <ChatInput
          inputValue={inputValue}
          onChange={handleInputChange}
          onKeyDown={handleKeyDown}
          onSend={sendMessage}
          isLoading={chatState.isLoading}
          forwardRef={inputRef}
        />
      </div>
    </ChatErrorBoundary>
  )
}
